<template>
  <div class="w-100 bg-white custom-chart-div t-min-height Piechrt text-center">
    <loader :isVisible="isLoading"></loader>
    <template v-if="PieWidgetData.length  === 0 && isLoading === false">
      <div class="text-danger no-record" :style="{ height: height + 150 + 'px' }" style="display: flex; align-items: center; justify-content: center">
        {{ $t("NoRecordfound") }}
      </div>
    </template>
    <template v-else>
      <div :id="'pieDiv_' + moduleCode + '_' + action.replace(/\s+/g, '_') + index" ref="chartContainer">
        <div
          :class="{
            'd-inline-block mt-2 reportBlock t-min-height align-center c3':
              PieWidgetData.length > 0,
            'd-inline-block mt-2 align-center c3':
              PieWidgetData.length == 0,
          }"
          :controller="moduleCode"
          :id="'div_PieChart_' + action.replace(/\s+/g, '_') + index"
        >
        </div>
        <div class="legend-container"></div>
      </div>
    </template>
    
  </div>
</template>
<script>
import "d3";
import "c3";
import DataService from "../../../services/DataService";
export default {
  props: {
    reportName: null,
    chartType: String, // Set the type
    // selectedChartType: String,
    dashboardid: Number,
    moduleCode: null,
    fromDate: null,
    toDate: null,
    filterData: "",
    index: null,
    width: null,
    height: null,
    reportId: null,
    resizeheight: null,
    isSystemReport: null,
  },
  data() {
    return {
      isLoading: true,
      action: this.reportName,
      charT_TYPE: "pie",
      PieWidgetData: [],
      HeaderData: null,
      showAddDashboardReport: false,
      chart: null, // Add a chart property to store the C3.js chart instance
    };
  },
  watch: {
    width: function () {
      this.bindWidget(this);
    },
    height: function () {
      this.bindWidget(this);
    },
    toDate: function () {
      this.FetchData();
    },
    fromDate: function () {
      this.FetchData(); 
    },
    filterData: function () {
      this.FetchData(); 
    },
  },

  created: function () {
    this.FetchData();
  },
  methods: {
    ViewReport(reportId) {
      const encodedReportID = btoa(JSON.stringify({ ReportID: reportId }));
      const route = '/Report/viewcustomreport/' + encodedReportID;
      // Open the route in a new tab
      window.open(route, '_blank');
    },
    FetchData: function () {
      
      var vueObj = this;
      vueObj.isLoading = true;
      // var params = `moduleName=${vueObj.moduleName}&reportName=Lead_By_Industry&fromDate=2023-03-20&toDate=2023-03-26&usersDash=`;
      var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
      var params = `moduleName=${vueObj.moduleCode}&currentChartType=${vueObj.charT_TYPE}&dashboardId=${vueObj.dashboardid}&reportName=${vueObj.reportName}&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=&SearchFilter=${vueObj.filterData}`;
      DataService.GetDashboardData(params).then(function (response) {
        if (response.data != null && response.data != "") {
          if (response.data.status == "Success") {
            // if (vueObj.widgetInfo.charT_TYPE == "table" && response.data.result != null) {
            //     vueObj.HeaderData = response.data.result.headers;
            // }
            if (response.data.result != null) {
              vueObj.PieWidgetData = response.data.result.data;
              vueObj.bindWidget(vueObj);
            }
          }
        }
        vueObj.$emit("set-widget-data", vueObj.PieWidgetData);
        vueObj.isLoading = false;
      });
    },
    bindWidget: function (vueObj) {
      if (vueObj.charT_TYPE && vueObj.PieWidgetData.length > 0) {
        var xCategory = "";
        var yCategory = "";
        var arrAUP = [];
        var legendArr = []
        // eslint-disable-next-line no-undef
        $.each(vueObj.PieWidgetData, function (index, element) {
          arrAUP.push([
            element.NAME + " (" + element.VALUE + ")",
            element.VALUE,
          ]);
          legendArr.push(
            element.NAME + " (" + element.VALUE + ")",
          );
        });
        var selector = "#div_PieChart_" + vueObj.action.replace(/\s+/g, '_') + vueObj.index;
        // eslint-disable-next-line no-undef
        $(selector).html("");
        let c3Obj = {
          bindto: selector,
          data: {
            columns: arrAUP,
            type: "pie",
            empty: {
              label: {
                position: "right",
                text: vueObj.$t("NoRecordfound"),
              },
            },
          },
          tooltip: {
            grouped: false
          },
          pie: {
            label: {
              format: function (value) {
                // eslint-disable-next-line no-undef
                return d3.format("")(value);
              },
            },
          },
          size: {
            height: this.resizeheight ? this.resizeheight * 40 : this.height + 100,
          },
          legend: {
            show: false,
          },
          color: {
            pattern: [
              "#23A4FF",
              "#FF6B6B",
              "#5FB49C",
              "#776274",
              "#916953",
              "#28587B",
              "#F4B701",
              "#272635",
              "#AEE5D8",
              "#FFC482",
              "#3C6E71",
              "#9FA0FF",
              "#D1B1CB",
              "#95B2B8",
              "#EF8354",
              "#72D9C8",
              "#C6EBBE",
              "#56203D",
              "#A09CB0",
              "#5C573E",
            ],
          },
          axis: {
            x: {
              label: {
                text: "",
                position: "outer-center",
              },
              categories: [xCategory],
              type: "category",
            },
            y: {
              label: {
                text: yCategory,
                position: "outer-center",
              },
            },
          },
        };
        // eslint-disable-next-line no-undef
        var Piechrt = c3.generate(c3Obj);
        // eslint-disable-next-line no-undef
        $(selector).data("c3-chart", Piechrt);
        function toggle(id) {
          Piechrt.toggle(id);
        }
        
        if ($('#pieDiv_' + vueObj.moduleCode + '_' + vueObj.action.replace(/\s+/g, '_') + vueObj.index + ' .legend-container').find('.legend').length === 0) {
          d3.select('#pieDiv_' + vueObj.moduleCode + '_' + vueObj.action.replace(/\s+/g, '_') + vueObj.index + ' .legend-container')
            .insert('div', '.chart').attr('class', 'legend').selectAll('div')
            .data(legendArr)
            .enter().append('div')
            .attr('data-id', function(id) {
              return id;
            })
            .html(function(id) {
              return id;
            })
            .each(function(id) {
              d3.select(this).append('span').style('background-color', Piechrt.color(id));
            })
            .on('mouseover', function(id) {
              Piechrt.focus(id);
            })
            .on('mouseout', function(id) {
              Piechrt.revert();
            })
            .on('click', function(id) {
              $(this).toggleClass("c3-legend-item-hidden")
              Piechrt.toggle(id);
            });
        }
      }
    },
  },
};
</script>
<style src="c3/c3.css"></style>
<!-- <style>
    .custom-chart-div.Piechrt .reportBlock {
        transform: initial;
        height: inherit;
    }
</style> -->
<style>

.legend span {
  display: inline-block;
  margin-left: 7px;
  margin-right: 7px;
  padding: 5px;
}
.legend-container .legend div {
  display: inline-block;
    line-height: 10px;
    margin-right: 6px;
}
.legend-container .legend{
  max-height: 80px;
    overflow-y: auto;
    font-size: 10px;
}
.view-report{
  margin-left: 10px;
  margin-top: 10px;
  text-decoration: underline;
  color: #1f77b4;
  font-weight: 400;
  cursor: pointer;
  float: left;
}
</style>
