<template>
  <div class="modal my-popups centre fade" id="modalprojectquickviewpopsharereport" role="dialog" v-cloak>
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" v-on:click="CloseShareReportView()" data-dismiss="modal">
            <i class="fa fa-times" aria-hidden="true"></i>
          </button>
          <h4 class="modal-title text-truncate" style="line-height: 26px" id="myModalLabel">
            Share Dashboard
          </h4>
        </div>
        <div class="modal-body">
          <div class="container-fluid p-0">
            <div class="form-group">
              <label>Share Dashboard with User/Roles:</label>
              <div class="d-flex input-group">
                <label class="form-check form-check-inline">
                  <div class="custom-control custom-radio">
                    <input name="" id="Sharewithtype_0" type="radio" class="custom-control-input" v-model="selectedOption"
                      value="USER" @change="getListData" />
                    <label for="Sharewithtype_0" class="custom-control-label">User</label>
                  </div>
                </label>
                <label class="form-check form-check-inline">
                  <div class="custom-control custom-radio ">
                    <input name="" id="Sharewithtype_1" type="radio" class="custom-control-input" v-model="selectedOption"
                      value="ROLE" @change="getListData" />
                    <label for="Sharewithtype_1" class="custom-control-label">Roles</label>
                  </div>
                </label>
              </div>
            </div>
            <ValidationProvider ref="validationProvider" :rules="required" v-slot="{ errors }">
              <Multiselect v-if="selectedOption" :placeholder="placeholderText" :searchable="true" :multiple="true"
                :options="selectedLists" v-model="selectedUsersID" track-by="value" label="name">
                <span v-if="selectedOption == 'USER'" slot="noResult">No User were found.</span>
                <span v-else-if="selectedOption == 'ROLE'" slot="noResult">No Roles were found.</span>
              </Multiselect>
              <span class="invalid-feedback d-block">{{ errors.default }}</span>
              <!-- Add this line to check the entire errors array -->
            </ValidationProvider>
            <div class="d-flex form-group mb-0">
              <label class="d-flex align-items-center mr-3">
                <input type="checkbox" class="mr-2" v-model="isEditable" /> Is
                Editable?
              </label>

              <label class="d-flex align-items-center mr-3">
                <input type="checkbox" class="mr-2" v-model="isShareable" /> Is
                Shareable?
              </label>
            </div>
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-right mb-2 p-0">
              <a href="javascript:;" data-tour="step: 1; title: enter DealDetail; content: AddDealDetailContent"
                class="btn btn-success float-right" title="Save" v-on:click="saveShareReport()"><em
                  class="fa fa-save pr-2"></em> {{ $t('Save') }}
              </a>
            </div>
            <div class="table-responsive w-100 float-left">
              <div class="heading-border">
                <h5 class="h5 my-2 px-3">
                  <span>{{ $t("SharedAlready") }}</span>
                </h5>
              </div>
              <div class="table-container" ref="tableContainer">
                <table class="table table-bordered">
                  <thead>
                    <tr>
                      <th v-for="column in displayedColumns" :key="column">{{ column }}</th>
                      <th>Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                     
                    <template v-if="UserListingData.ShareData && UserListingData.ShareData.length > 0">
                      <tr v-for="(item, index) in UserListingData.ShareData" :key="item.ROWNUMBER">
                        <td>
                          <div class="conversation-box_custreps">
                            <div class="name-square" v-if="(item.Ref_Code === 'USER')">
                              <em class="fa fa-user text-white fa-2x" aria-hidden="true"></em>
                            </div>
                            <div class="role-square" v-if="(item.Ref_Code === 'ROLE')">
                              <em class="fa fa-user-circle text-white fa-2x" aria-hidden="true"></em>
                            </div>
                            <div class="conversation-header">
                              {{ item.UserName }}
                            </div>
                            <div class="conversation-header">
                              <small>
                                {{ item.Ref_Code }}
                              </small>
                            </div>
                          </div>
                        </td>
                        <td>
                          <label class="switch auto" v-if="item.Created_By == GetUserInfo.ID || item.Is_Editable == IsEdit">
                            <input id="" class="form-check-input rdoticketfor custom-control-input dynamic" type="checkbox"
                              v-model="item.Is_Editable" :checked="item.Is_Editable"
                              @change="handleStatusChange(item, 'editable')" />
                            <div class="slider round">
                              <span class="slider-yes">Yes</span>
                              <span class="slider-no">No</span>
                            </div>
                          </label>
                        </td>
                        <td>
                          <label class="switch auto" v-if="item.Created_By == GetUserInfo.ID || item.Is_Shareable == IsShare">
                            <input id="" class="form-check-input rdoticketfor custom-control-input dynamic" type="checkbox"
                              v-model="item.Is_Shareable" v-bind:checked="item.Is_Shareable"
                              @change="handleStatusChange(item, 'shareable')" />
                            <div class="slider round">
                              <span class="slider-yes">Yes</span>
                              <span class="slider-no">No</span>
                            </div>
                          </label>
                        </td>
                        <td>
                          <button @click="deleteListItem(item)" class="delete-button"><em
                              class="fa fa-trash text-danger ml-4"></em></button>
                        </td>
                      </tr>
                    </template>
                    <template v-else>
                      <!-- <tr> -->
                        <td class="text-danger no-record">No Record(s) found</td>
                      <!-- </tr> -->
                    </template>
                    <!-- <tr v-if="UserListingData.ShareData.length == 0">
                      <td colspan="4" class="text-center text-danger no-record">No Record(s) Found.</td>
                    </tr> -->
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <loader :isVisible="isLoading"></loader>
  </div>
</template>
<script>
import DataService from "../../services/DataService";
import { ValidationProvider } from "vee-validate";
import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());
export default {
  components: {
    ValidationProvider,
  },
  props: {
    dashboardid: null,
    moduleCode: null,
  },
  data: function () {
    return {
      isLoading: false,
      selectedOption: "USER",
      displayedColumns: ["User Name", "Is Editable", "Is Shareable"],
      selectedUsersID: [],
      errorUserNotSelected: "",
      isEditable: false,
      isShareable: false,
      PageSize: 10,
      PageNumber: 1,
      isPaged: 1,
      TotalPages: 0,
      reportGroups: [],
      SystemReport: [],
      filteredObjects: [],
      selectedLists: [],
      idsString: null,
      SystemReportFiltered: [],
      reportbydashbord: null,
      UserListingData: [],
      filteredObjects: [],
      IsEdit:true,
      IsShare:true,
    };
  },
  created() {
    this.GetDynamicReport();
    this.getListData();
    this.alreadySharedData();

  },
  mounted() {
    // eslint-disable-next-line no-undef
    $("#modalprojectquickviewpopsharereport").modal("show");
    this.CheckBoxBootstrap();

  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkTableWidth);
  },
  computed: {
    placeholderText() {
      if (this.selectedOption === 'USER') {
        return 'Select Users';
      } else if (this.selectedOption === 'ROLE') {
        return 'Select Roles';
      } else {
        return 'Select'; // Default placeholder text if selectedOption is neither 'USER' nor 'ROLE'
      }
    }
  },
  methods: {
    checkTableWidth() {
      const tableContainer = this.$refs.tableContainer;
      if (tableContainer) {
        const containerHeight = tableContainer.getBoundingClientRect().height;
        const itemHeight = 75.1 /* Calculate the width of a single item */;
        const numItems = 5 /* Count the number of items in a row */;
        const totalHeight = itemHeight * numItems;
        if (containerHeight > totalHeight) {
          tableContainer.classList.add('scrollable');
        } else {
          tableContainer.classList.remove('scrollable');
        }
      }
    },

    async handleStatusChange(item, type) {
      console.log(type);
      var vm = this;
      vm.confirmR(vm.$t("ConfirmUpdateSettings"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
        // let params = `moduleName=${vm.moduleCode}&Id=${item.Id}`
        let params = `moduleName=${vm.moduleCode}&Id=${item.Id}&dashboardId=${vm.dashboardid}&refId=${vm.selectedUsersID}&refCode=${vm.selectedOption}&isEditable=${item.Is_Editable}&isShareable=${item.Is_Shareable}`;
        DataService.SaveDashboardShareReport(params).then(async function (response) {
          if (response.status == 200) {
            vm.ShowAlert(vm.$t('SettingsUpdate', [vm.$t('dashboard')]), "success", true, vm.$t("Alert"));
          }
          else {
            vm.ShowAlert(vm.$t('SettingsError', [vm.$t('dashboard')]), "success", true, vm.$t("Alert"));
          }
        });
      }, function () {
        if (type === 'editable') {
          item.Is_Editable = !item.Is_Editable;
        } else if (type === 'shareable') {
          item.Is_Shareable = !item.Is_Shareable;
        }
      });
    },

    async alreadySharedData() {
      this.isLoading = true;
      try {
        const params = `dashboardId=${this.dashboardid}&pageSize=${this.PageSize}&pageNum=${this.PageNumber}&sortBy=${this.SortBy}&sortExp=${this.SortExp}`;
        const response = await DataService.GetDashboardShareData(params);
        this.UserListingData = JSON.parse(response.data.map((i) => i.Value));
        this.$nextTick(() => {
          this.checkTableWidth();
          window.addEventListener('resize', this.checkTableWidth);
        });
      } catch (error) {
        console.error('Error fetching shared data:', error);
      } finally {
        this.isLoading = false;
      }
    },

    // async alreadySharedData() {
    //   var vueObj = this;
    //   vueObj.isLoading = true;
    //   let params = `dashboardId=${vueObj.dashboardid}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}`;
    //   const response = await DataService.GetDashboardShareData(params);
    //   vueObj.UserListingData = JSON.parse(response.data.map((i) => i.Value));
    //   this.$nextTick(() => {
    //     this.checkTableWidth();
    //     window.addEventListener('resize', this.checkTableWidth);
    //   });
    //   vueObj.isLoading = false;
    // },

    async deleteListItem(item) {
      var vm = this;
      vm.isLoading = true;
      vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
        let params = `moduleName=${vm.moduleCode}&Id=${item.Id}`
        DataService.DeleteAssignedShareDashboard(params).then(async function (response) {
          if (response.status == 200) {
            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('dashboard')]), "success", true, vm.$t("Alert"));
            vm.PageNumber = parseInt(vm.PageNumber) - 1;
            if (vm.PageNumber <= 0) {
              vm.PageNumber = 1;
            }
            vm.CloseShareReportView();
          }
          else {
            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('dashboard')]), "success", true, vm.$t("Alert"));
          }
        });
      });
      vm.isLoading = false;
    },
    async getListData() {
      try {
        let params;
        if (this.selectedOption == "USER") {
          this.selectedUsersID = [];
          params = `moduleName=${this.moduleCode}&id=${this.dashboardid}&type=DASHBOARD_USERS`;
        } else {
          this.selectedUsersID = [];
          params = `moduleName=${this.moduleCode}&id=${this.dashboardid}&type=DASHBOARD_ROLES`;
        }
        // Fetch data from the API
        const response = await DataService.GetDDLData(params);
        // Update the selectedLists data property with the fetched data
        this.selectedLists = response.data.DATA;
      } catch (error) {
        console.error("API error:", error);
      }
    },
    async saveShareReport() {
      try {
        var vm = this;
        let params;
        if (this.selectedUsersID && this.selectedUsersID.length > 0) {
          const selectedUserValues = vm.selectedUsersID.map(
            (user) => user.value
          );
          // Convert selectedUsersID array to a comma-separated string
          const selectedUserValuesString = selectedUserValues.join(",");
          params = `moduleName=${vm.moduleCode}&dashboardId=${vm.dashboardid}&refId=${selectedUserValuesString}&refCode=${vm.selectedOption}&isEditable=${vm.isEditable}&isShareable=${vm.isShareable}`;
          // Fetch data from the API
          const response = await DataService.SaveDashboardShareReport(params);
          if (response.data[0].Status) {
            vm.ShowAlert(
              vm.$t("AddedSuccessShared", [vm.$t("Dashboard")]),
              "success",
              true,
              vm.$t("Alert")
            );
          } else {
            vm.ShowAlert(
              vm.$t("Failure", [vm.$t("Dashboard")]),
              "warning",
              true,
              vm.$t("Alert")
            );
          }
          vm.CloseShareReportView();
          vm.isLoading = false;
        } else {
          // Display a validation message if selectedUsersID is empty
          if (vm.$refs.validationProvider) {
            vm.$refs.validationProvider.setErrors({
              default: "Please Select User",
            });
          }
        }
      } catch (error) {
        console.error("API error:", error);
      }
    },
    SelectSystemReports: function (event) {
      const clickedLabel = event.currentTarget;
      clickedLabel.classList.toggle("active");
    },
    CloseShareReportView: function () {
      var vm = this;
      vm.$parent.showShareReport = false;
      $(".modal-backdrop").remove();
    },
    GetDynamicReport() {
      var vm = this;
      var param = `moduleName=${this.moduleCode}`;
      DataService.GetDynamicReport(param).then(function (response) {
        vm.reportGroups = response.data.UserReport;
        vm.SystemReport = response.data.SystemReport;
        setTimeout(() => {
          vm.CheckBoxBootstrap();
          vm.GetDashboardReport();
        }, 1000);
      });
    },
    GetDashboardReport: function () {
      var vm = this;
      var param = `dashboardId=${vm.dashboardid}&moduleName=${vm.moduleCode}`;
      DataService.GetDashboardReport(param).then(function (response) {
        vm.reportbydashbord = response.data;
        response.data.forEach(function (obj) {
          var privilegeId = obj.reportId;
          var element = document.getElementById(privilegeId);
          if (element) {
            element.classList.add("active");
          }
        });
      });
    },
    filterObjects() {
      var vm = this;
      vm.idsToFilter = vm.idsString.split(",");
      vm.filteredObjects = vm.SystemReportFiltered.filter((obj) =>
        vm.idsToFilter.includes(obj.privilege_id.toString())
      );
      return vm.filteredObjects;
    },
    saveReport: function () {
      var vm = this;
      // this.isLoading=true;
      var reportArr = [];
      vm.SystemReportFiltered = vm.SystemReport;
      const elementsWithSystemReportClass =
        document.querySelectorAll(".SystemReport");
      const elementsWithBothClasses = Array.from(
        elementsWithSystemReportClass
      ).filter((element) => {
        return element.classList.contains("active");
      });
      const ids = elementsWithBothClasses.map((element) => element.id);
      vm.idsString = ids.join(",");
      var finlobjre = vm.filterObjects();
      for (const key in finlobjre) {
        reportArr.push({
          reportId: finlobjre[key].privilege_id,
          chartType: finlobjre[key].chart_type,
          displayOrder: key,
          xaxis: 0,
          yaxis: 0,
          is_system_report: 1,
        });
      }
      for (let index = 0; index < vm.reportGroups.length; index++) {
        // eslint-disable-next-line no-undef
        $("#tblReport" + index + " .chkItems:checked")
          .not("[id^='chkAll']")
          .each(function () {
            // eslint-disable-next-line no-undef
            if ($(this).attr("data-reportid")) {
              reportArr.push({
                // eslint-disable-next-line no-undef
                reportId: $(this).attr("data-reportid"),
                // eslint-disable-next-line no-undef
                chartType: $(this)
                  .parent()
                  .parent()
                  .parent()
                  .find(".rdoticketfor")
                  .is(":checked")
                  ? "Graph"
                  : "Table",
                displayOrder: index,
                xaxis: 0,
                yaxis: 0,
                is_system_report: 0,
              });
            }
          });
      }
      var data = {
        moduleName: "CRM",
        reports: reportArr,
        dashboardId: vm.dashboardid,
      };
      var objectPostString = JSON.stringify(data);
      DataService.SaveReport(objectPostString).then(function (response) {
        if (response.data.status == "Success") {
          vm.ShowAlert(
            vm.$t("AddedSuccess", [vm.$t("Report")]),
            "success",
            true,
            vm.$t("Alert")
          );
          vm.$parent.GetDashboard();
          vm.GetDashboardReport();
          // eslint-disable-next-line no-undef
          $("#modalprojectquickviewpopsharereport").modal("hide");
        } else {
          vm.ShowAlert(
            vm.$t("error", [vm.$t("Report")]),
            "warning",
            true,
            vm.$t("Alert")
          );
        }
        vm.isLoading = true;
      });
    },
  },
};
</script>
<style scoped>
.delete-button {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
  outline: none;
  /* Optional: Remove the outline when the button is focused */
}

/* Adjust the size and color of the icon as needed */
.delete-button .fa {
  font-size: 1.5rem;
  color: red;
}

.accordion>.card>.card-header {
  border-radius: 0;
  margin-bottom: 10px;
}

.custom-reports-icons {
  min-width: inherit;
  height: 104px;
  width: 33%;
}

.Textstyle {
  font-size: 1.25rem;
  line-height: 40px;
  text-transform: capitalize;
}

.scrollable {
  overflow-y: auto;
  max-height: 435px;
}
</style>
