<template>
  <div class="w-100 bg-white t-min-height donut custom-chart-div text-center">
    <loader :isVisible="isLoading"></loader>
    <template v-if="DonutWidgetData.length  === 0 && isLoading === false">
      <div class="text-danger no-record" :style="{ height: height + 150 + 'px' }" style="display: flex; align-items: center; justify-content: center">
        {{ $t("NoRecordfound") }}
      </div>
    </template>
    <template v-else>
      <div :id="'DonutDiv_' + moduleCode + '_' + action.replace(/\s+/g, '_') + index">
        <div
          :class="DonutWidgetData.length > 0 ? 'd-inline-block mt-2 reportBlock align-center c3' : 'd-inline-block mt-2 align-center c3'"
          :controller="moduleCode"
          :id="'div_WidgetChart_' + action.replace(/\s+/g, '_') + index"
          :data-test="DonutWidgetData.length"
          :style="{ 'width:60%; float:left; text-align:center;' : DonutWidgetData.length, 'width:100%;text-align:center;': DonutWidgetData.length == 0 }">
        </div>
        <div class="legend-container"></div>
        <!-- <div v-if="isSystemReport == false" class="view-report" @click="ViewReport(reportId)">View Report</div> -->
      </div>
    </template>
  </div>
</template>
<script>
import "d3";
import "c3";
import "c3/c3.min.css";
import DataService from "../../../services/DataService";
export default {
  props: {
    reportName: null,
    chartType: null,
    dashboardid: null,
    moduleCode: null,
    fromDate: null,
    toDate: null,
    filterData: "",
    index: null,
    width: null,
    height: null,
    reportId: null,
    isSystemReport: null,
    resizeheight: null,
  },
  data() {
    return {
      isLoading: true,
      action: this.reportName,
      charT_TYPE: "donut",
      DonutWidgetData: [],
      HeaderData: null,
      showAddDashboardReport: false,
      ChartName: null,
    };
  },
  watch: {
    width: 'handleResize',
    height: 'handleResize',
    toDate: 'handleFetchData',
    fromDate: 'handleFetchData',
    filterData: 'handleFetchData',
    chartType: 'handleFetchData'
  },
  created: function () {
    var vueObj = this;
    vueObj.FetchData();
  },
  mounted() {
    window.addEventListener('resize', this.handleResize);
  },
  destroyed() {
    // Remove event listener when component is destroyed
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    ViewReport(reportId) {
      const encodedReportID = btoa(JSON.stringify({ ReportID: reportId }));
      const route = '/Report/viewcustomreport/' + encodedReportID;
      // Open the route in a new tab
      window.open(route, '_blank');
    },
    async FetchData() {
      var vueObj = this;
      vueObj.isLoading = true;
      var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
      var url = `moduleName=${vueObj.moduleCode}&currentChartType=${vueObj.charT_TYPE}&dashboardId=${vueObj.dashboardid}&reportName=${vueObj.reportName}&&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=&SearchFilter=${vueObj.filterData}`;
      await DataService.GetDashboardData(url).then(function (response) {
        if (response.data != null && response.data != "") {
          if (response.data.status == "Success") {
            if (response.data.result != null) {
              vueObj.DonutWidgetData = response.data.result.data;
              // if (vueObj.DonutWidgetData.length > 0) {
                vueObj.bindWidget(vueObj);
              // }
            }
          }
        }
        vueObj.$emit("set-widget-data", vueObj.DonutWidgetData);
        setTimeout(() => {
          vueObj.isLoading = false;
        }, 3000); // 3000 milliseconds = 3 seconds
      });
    },
    bindWidget: function (vueObj) {
      if (vueObj.charT_TYPE && vueObj.DonutWidgetData.length > 0) {
        var chartType = this.charT_TYPE;
        var arrAUP = [];
        // eslint-disable-next-line no-undef
        $.each(vueObj.DonutWidgetData, function (index, element) {
          if (vueObj.action == "LoggedHoursByProject") {
            arrAUP.push([
              element.NAME + " (" + element.VALUE + " h)",
              element.VALUE,
            ]);
          } else {
            arrAUP.push([
              element.NAME + " (" + element.VALUE + ")",
              element.VALUE,
            ]);
          }
        });
        var xCategory = "";
        var yCategory = "";
        var selector = "#div_WidgetChart_" + vueObj.action.replace(/\s+/g, '_') + vueObj.index;
        // eslint-disable-next-line no-undef
        $(selector).html("");
        // eslint-disable-next-line no-undef
        var Donutchrt = c3.generate({
          bindto: selector,
          data: {
            columns: arrAUP,
            type: chartType,
            empty: {
              label: {
                position: "right",
                text: vueObj.$t("NoRecordfound"),
              },
            },
          },
          tooltip: {
            grouped: false
          },
          size: {
            height: this.resizeheight ? this.resizeheight * 40 : this.height + 100,
          },
          pie: {
            label: {
              format: function (value) {
                // eslint-disable-next-line no-undef
                return d3.format("")(value);
              },
            },
          },
          // size: {
          //   width: chartWidth,
          //   height: chartHeight,
          // },
          legend: {
            show: true,
            position: "bottom",
          },
          color: {
            pattern: [
              "#23A4FF",
              "#FF6B6B",
              "#5FB49C",
              "#776274",
              "#916953",
              "#28587B",
              "#F4B701",
              "#272635",
              "#AEE5D8",
              "#FFC482",
              "#3C6E71",
              "#9FA0FF",
              "#D1B1CB",
              "#95B2B8",
              "#EF8354",
              "#72D9C8",
              "#C6EBBE",
              "#56203D",
              "#A09CB0",
              "#5C573E",
            ],
          },
          axis: {
            rotated: false,
            x: {
              label: {
                text: "",
                position: "outer-center",
              },
              categories: [xCategory],
              type: "category",
            },
            y: {
              label: {
                text: yCategory,
                position: "outer-center",
              },
            },
          },
        });
        // eslint-disable-next-line no-undef
        $(selector).data("c3-chart", Donutchrt);
      }
    },
    handleResize() {
      this.bindWidget(this);
    },
    handleFetchData() {
      this.FetchData();
    }
  },
};
</script>
<style src="c3/c3.css"></style>
